/* =========================
   üìå Styles du Layout
========================= */

.layout {
   display: flex;
   flex-direction: column;
   min-height: 100vh;
   width: 100vw;
   background-color: var(--primary-back-color);
   overflow-x: hidden;
 }
 
 /* ‚úÖ D√©calage pour ne pas que le contenu soit sous le header */
 .main {
   flex-grow: 1;
   margin-top: 1vh !important; /* üî• Ajustement global */
   padding: 20px;
 }
 
 /* üìå Conteneur du contenu centr√© */
 .content-container {
   width: 100%;
   max-width: 1200px;
   background-color: var(--primary-back-color);
   padding: 20px;
   box-sizing: border-box;
   margin: 0 auto; /* Centre le contenu */
 }
 
 /* =========================
    üìå Ajustements Responsive du Layout
 ========================= */
 
 @media (width <= 768px) {
   .main {
     margin-top: 115vh !important; /* üî• Ajustement pour √©viter le chevauchement */
     min-height: 115vh !important; 
     display: flex;
     flex-direction: column;
     justify-content: center;
   }
 }
 